import './App.css';
import axios from 'axios';
import { useState, useEffect } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import { DateTime } from "luxon";
import markerIconPng from "leaflet/dist/images/marker-icon.png"
import {Icon} from 'leaflet'
import Card from 'react-bootstrap/Card';

//How to deploy on GitHub Pages: https://dev.to/yuribenjamin/how-to-deploy-react-app-in-github-pages-2a1f

function App() {
  const API_KEY = process.env.REACT_APP_API_KEY;
  const url = 'https://geo.ipify.org/api/v2/country,city?apiKey='+API_KEY+'&ipAddress='
  
  const [loading, setLoading] = useState(false);
  const [ipNum, setIp] = useState('not found');
  const [latlng, setLatlng] = useState([51.505, -0.09]);

  useEffect(() => loadIp(), []);

  const now = DateTime.now().toLocaleString(DateTime.DATETIME_MED_WITH_WEEKDAY);
  const zone = DateTime.now().zoneName;

  const loadIp = async () => {
    setLoading(true);
    const response = await axios.get(url);
    console.log(response);
    setIp(response.data);
    setLatlng([response.data.location.lat, response.data.location.lng]);
    setLoading(false);
  }
  console.log(latlng);



  return (
    <div className="App">
      <h1>What is my IP-address?</h1>
      {loading ?
      <p id="loading">Loading...</p>
      :
      <div className="body">
      <Card>
        <Card.Body className="ipField">
        <Card.Title style={{textDecorationLine: 'underline'}}>My IP:</Card.Title>
        <Card.Text style={{fontWeight: 'bold'}}>{ipNum?.ip}</Card.Text>
        <Card.Text>{ipNum?.location?.city}</Card.Text>
        <Card.Text>{ipNum?.location?.region}, {ipNum?.location?.country}</Card.Text>
        <Card.Text>{now}</Card.Text>
        <Card.Text>Timezone: {zone}</Card.Text>
        <Card.Img src={`https://flagcdn.com/80x60/${ipNum?.location?.country.toLowerCase()}.png`} srcset={`https://flagcdn.com/h40/${ipNum?.location?.country?.toLowerCase()}.png 2x,
        https://flagcdn.com/h60/${ipNum?.location?.country.toLowerCase()}.png 3x`} className='map' alt="flag" />
        </Card.Body>
      </Card>
      <MapContainer center={latlng} zoom={15} scrollWheelZoom={false}>
  <TileLayer
    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
  />
  <Marker position={latlng} icon={new Icon({iconUrl: markerIconPng, iconSize: [25, 41], iconAnchor: [12, 41]})}>
    <Popup>
      You are here <br />
    </Popup>
  </Marker>
</MapContainer>
</div>
      }
    </div>
  );
}

export default App;

